@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Narrow&display=swap);
.Devtools {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 33.3%;
  padding: 1rem 4rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  z-index: 1;
}

.Devtools__input {
  background: none;
  color: #AAAAAA;
  font-size: inherit;
  border-radius: .25rem;
  font-family: monospace;
  padding: 0;
  margin: 0;
  border: 0;
  line-height: 25px;
}

.Devtools__input:focus {
  outline: none;
}

.Devtools__log {
  font-family: monospace;
  font-size: inherit;
  color: #AAAAAA;
  white-space: pre;
  margin-bottom: auto;
}

.Devtools__log div {
  line-height: 25px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.Main {
  display: flex;
  flex: 1 1;
  overflow: hidden;
  position: relative;
}

.Static {
  flex: 1 1;
  padding: 2rem 4rem;
}

.Toolbar {
  pointer-events: none;
  display: flex;
  padding: 10px;
}

.Toolbar > * {
  pointer-events: all;
}

.Toolbar * + * {
  margin-left: 10px;
}

.Button {
  display: inline-block;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 1rem;
  color: var(--foreground-color);
  border: 1px solid transparent;
}

.Button:hover {
  opacity: 0.7;
}

.Button--is-muted {
  opacity: .5;
}

.Button--icon {
  line-height: 0;
}

.Button--text {
  padding: .3rem;
}

.Button--is-active {
  border-bottom-color: var(--foreground-color);
}

.Button[disabled] {
  pointer-events: none;
}

.Header {
  justify-content: flex-end;
}

.Footer {
  justify-content: flex-end;
}

.TextGlitch {
  position: relative;
  margin: 0 auto;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.TextGlitch::before,
.TextGlitch::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  background-color: inherit;
  clip: rect(0, 100vw, 0, 0);
}

.TextGlitch::before {
  left: 0.06em;
  text-shadow: 1px 0 green;
  animation: glitch-effect 3s infinite linear alternate-reverse;
}

.TextGlitch::after {
  left: 0.03em;
  text-shadow: -1px 0 red;
  animation: glitch-effect 2s infinite linear alternate-reverse;
}

@-webkit-keyframes glitch-effect {
  0% { clip: rect(0.04em, 100vw, 0.58em, 0); }
  20% { clip: rect(0.73em, 100vw, 0.07em, 0); }
  40% { clip: rect(0.81em, 100vw, 0.34em, 0); }
  60% { clip: rect(0.66em, 100vw, 1.1em, 0); }
  80% { clip: rect(1.31em, 100vw, 0.99em, 0); }
  100% { clip: rect(0.07em, 100vw, 1.11em, 0); }
}

@keyframes glitch-effect {
  0% { clip: rect(0.04em, 100vw, 0.58em, 0); }
  20% { clip: rect(0.73em, 100vw, 0.07em, 0); }
  40% { clip: rect(0.81em, 100vw, 0.34em, 0); }
  60% { clip: rect(0.66em, 100vw, 1.1em, 0); }
  80% { clip: rect(1.31em, 100vw, 0.99em, 0); }
  100% { clip: rect(0.07em, 100vw, 1.11em, 0); }
}

.Graphics {
  flex: 1 1;
  display: flex;
}

.Graphics--perspective {
  perspective: var(--perspective);
}

.Graphics__layer-container {
  margin: auto;
  width: 80%;
  height: 80%;
  position: relative;
  transition: all .05s ease;
}

.Graphics__layer {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  pointer-events: none;
}

.Graphics__layer-container--perspective {
  transform-style: preserve-3d;
  transform:
    rotateX(var(--deg-x))
    rotateY(var(--deg-y))
    translateZ(0);
}

.Graphics__layer-container--perspective .Graphics__layer {
  transform: translateZ(var(--translate-z));
}

.Graphics__layer-container--parallax-vertical .Graphics__layer {
  transform: translateY(var(--translate-y));
}

.Graphics__layer-container--parallax-horizontal .Graphics__layer {
  transform: translateX(var(--translate-x));
}

.Graphics__layer-container--parallax .Graphics__layer {
  transform:
    translateX(var(--translate-x))
    translateY(var(--translate-y))
    translateZ(0);
}

@-webkit-keyframes wave {
  50% { transform: translateY(.5em) rotate(5deg); }
}

@keyframes wave {
  50% { transform: translateY(.5em) rotate(5deg); }
}

.Boat {
  --animation-duration: 4s;
  -webkit-animation: wave var(--animation-duration) calc(var(--animation-duration) * 0) ease infinite;
          animation: wave var(--animation-duration) calc(var(--animation-duration) * 0) ease infinite;
}

.Boat:nth-of-type(5n + 1) { -webkit-animation-delay: calc(var(--animation-duration) * 0); animation-delay: calc(var(--animation-duration) * 0); }
.Boat:nth-of-type(5n + 2) { -webkit-animation-delay: calc(var(--animation-duration) * .5); animation-delay: calc(var(--animation-duration) * .5); }
.Boat:nth-of-type(5n + 3) { -webkit-animation-delay: calc(var(--animation-duration) * 1); animation-delay: calc(var(--animation-duration) * 1); }
.Boat:nth-of-type(5n + 4) { -webkit-animation-delay: calc(var(--animation-duration) * 1.5); animation-delay: calc(var(--animation-duration) * 1.5); }
.Boat:nth-of-type(5n + 5) { -webkit-animation-delay: calc(var(--animation-duration) * 2); animation-delay: calc(var(--animation-duration) * 2); }

.Chat {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.Chat__options {
  min-height: 150px;
  margin-bottom: 100px;
  text-align: right;
}

.PageMagic8Ball__answer {
  opacity: 0;
  transform: rotate(-20deg) scale(0.93);
  transition: all .7s ;
  transition-timing-function: ease-out, cubic-bezier(.82, .1, .14, 1.12);
  pointer-events: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.PageMagic8Ball__answer-text {
  z-index: 1;
  position: relative;
  margin: auto;
  text-transform: uppercase;
  font-size: .95rem;
  font-weight: bold;
  text-shadow: -1px -1px 1px #000,
    1px 1px 1px rgba(255,255,255,.1);
  opacity: .7;
  font-family: 'PT Sans Narrow', sans-serif;
}

.PageMagic8Ball__answer-text::first-letter {
  font-size: 1.2rem;
}

.PageMagic8Ball__answer--ready {
  opacity: 1;
  transform: rotate(0) scale(1);
}

:root {
  --perspective: 60vmax;
  --translate-z-delta: 50px;
  --translate-x-delta: 0;
  --translate-y-delta: 0;
  --deg-y: 0;
  --deg-x: 0;
  --font-size: 18px;
  --background-color: #222222;
  --foreground-color: #EEEEEE;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  color: #EEEEEE;
  color: var(--foreground-color);
  font-size: 18px;
  font-size: var(--font-size);
  line-height: 1.4;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

button {
  margin: 0;
  padding: 0;
  border: 0;
  background: 0;
  outline: none;
  font-size: inherit;
}

p {
  margin: 0 0 1rem 0;
}

body::before {
  z-index: -1;
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: #222222;
  background-color: var(--background-color);
  pointer-events: none;
  transition: background-color .5s ease;
}

#root {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.abzac::first-letter {
  padding-left: 3rem;
}

a {
  color: inherit;
  text-decoration: none;
}

